<template>
  <div class="InstallCourse">
    <HeaderNavigation />
    <div class="InstallCourseBox">
      <div
        class="CustomerDataDetailsHistoryBoxBg"
        :style="{
          backgroundImage: 'url(' + backgroundImage + ')',
          backgroundSize: 'cover',
          backgroundRepeat: 'round'
        }"
      >
        <div class="InstallCourseBoxTitle">
          <h1>{{ userInfoList.CustName }}·保養歷程</h1>
          <h2 @click="toInstallBasics()">回基本資料</h2>
        </div>
        <div class="box_wrap">
          <!-- <div class="list">
            <div class="t">原水水壓</div>
            <div class="b">
              <b>{{ imageList.WaterPressure }}</b>
              <small>kg/cm²</small>
            </div>
          </div> -->
          <div class="list">
            <div class="t">餘氯</div>
            <div class="b">
              <b>{{ imageList.Chlorine }}</b>
            </div>
          </div>
          <div class="list">
            <div class="t">原水TDS</div>
            <div class="b">
              <b>{{ imageList.TDS }}</b>
              <small>PPM</small>
            </div>
          </div>
          <div class="list">
            <div class="t">原水PH值</div>
            <div class="b">
              <b>{{ imageList.PH }}</b>
            </div>
          </div>
        </div>
      </div>
      <div class="CustomerDataDetailsHistoryBoxPadding">
        <div class="InstallCourseBoxHeader">
          <span>
            <img :src="userInfoList.ProductPictureUrl" alt="" />
          </span>
          <div>
            <h3>
              {{ userInfoList.ModelName }}
              <em>更換日：{{ userInfoList.IncidentDate }}</em>
            </h3>
            <!-- <h5>銷售金額：{{ userInfoList.Charge }}</h5> -->
            <h4>單號：{{ userInfoList.IncidentNo }}</h4>
            <h4>地址：{{ userInfoList.Address }}</h4>
            <h4>電話：{{ userInfoList.Mobile }}</h4>
          </div>
        </div>

        <div
          class="InstallCourseBoxContent"
          v-for="(item, index) in dataFormList"
          :key="index"
        >
          <img src="@/assets/image/InstallCourseB.png" alt="" />
          <div>
            <h5>淨水器濾心{{ item.Sequence }}號</h5>
            <h6
              v-for="(items, indexs) in item.FilterChangeHistoryList"
              :key="indexs"
            >
              <em>{{ items.Mfno }}</em>
              <i>{{ items.LastChangeDate }}</i>
              <strong>{{ items.Charge }}</strong>
            </h6>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  POST_GetIncident,
  POST_GetFilterChangeHistoryForDistribution,
  POST_GetInstallData
} from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "InstallCourse",
  components: {
    HeaderNavigation
  },
  data() {
    return {
      imageList: "",
      backgroundImage: require("@/assets/image/jpgbg.jpg"),
      userInfoList: {},
      dataFormList: []
    };
  },
  created() {
    this.GetIncident();
    this.GetInstallData();
  },
  methods: {
    GetInstallData() {
      var form = {
        Mfno: this.$route.query.WaterMachineMfno
      };
      POST_GetInstallData(form).then(response => {
        this.imageList = response.Data;
      });
    },
    // 获取信息
    GetIncident() {
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId")
      };
      POST_GetIncident(UofIds).then(response => {
        for (let i = 0; i < response.Data.length; i++) {
          if (response.Data[i].IncidentNo == this.$route.query.id) {
            this.userInfoList = response.Data[i];
            var a = response.Data[i].IncidentDate.split("T")[0];
            var s = a.split("-");
            this.userInfoList.IncidentDate =
              s[0] + "年" + s[1] + "月" + s[2] + "日";
            var ProductId = {
              ProductId: response.Data[i].ProductId
            };
            POST_GetFilterChangeHistoryForDistribution(ProductId).then(
              response => {
                this.dataFormList = response.Data;
              }
            );
          }
        }
      });
    },
    toInstallBasics() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.InstallCourse {
  width: 100%;
  .InstallCourseBox {
    padding: 0px 0 0;
    .CustomerDataDetailsHistoryBoxPadding {
      padding: 0 5.33%;
      margin-top: -50px;
    }
    .CustomerDataDetailsHistoryBoxBg {
      background-color: #1c76fe;
      padding: 22px 5.33%;
      min-height: 280px;
      .box_wrap {
        display: flex;
        flex-wrap: wrap;
        margin-top: 30px;
        .list {
          flex: 0 0 50%;
          margin-bottom: 14px;
          .t {
            font-size: 12px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 17px;
            margin-bottom: 6px;
          }
          .b {
            b {
              font-size: 28px;
              color: rgba(255, 255, 255, 1);
              line-height: 32px;
            }
            small {
              font-size: 12px;
              font-weight: 400;
              color: rgba(255, 255, 255, 1);
              line-height: 17px;
              margin-left: 18px;
            }
          }
        }
      }
    }
    .InstallCourseBoxTitle {
      display: flex;
      justify-content: space-between;
      h1 {
        font-size: 18px;
        font-weight: 500;
        color: #ffffff;
        line-height: 25px;
      }
      h2 {
        font-size: 15px;
        font-weight: 500;
        color: #ffffff;
        line-height: 21px;
        text-decoration: underline;
      }
    }
    .InstallCourseBoxHeader {
      margin-top: 10px;
      margin-bottom: 14px;
      padding: 10px 2.67%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      display: flex;
      align-items: center;
      span {
        display: inline-block;
        width: 58px;
        height: 58px;
        margin-right: 20px;
        text-align: center;
        img {
          max-width: 58px;
          max-height: 58px;
        }
      }
      div {
        h3 {
          font-size: 13px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 18px;
          margin-bottom: 10px;
          em {
            margin-left: 8px;
          }
        }
        h4 {
          font-size: 12px;
          font-weight: 500;
          color: rgba(134, 134, 134, 1);
          line-height: 17px;
        }
      }
    }
    .InstallCourseBoxContent {
      margin-bottom: 10px;
      padding: 19px 5.67%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      display: flex;
      img {
        width: 10px;
        height: 22px;
      }
      div {
        width: 100%;
        margin-left: 8px;
        h5 {
          font-size: 13px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 18px;
          margin-bottom: 7px;
        }
        h6 {
          width: 100%;
          font-size: 12px;
          font-weight: 500;
          color: rgba(134, 134, 134, 1);
          line-height: 17px;
          em {
            display: inline-block;
            width: 50%;
          }
          i {
            display: inline-block;
            width: 30%;
          }
          strong {
            text-align: center;
            display: inline-block;
            width: 20%;
          }
        }
      }
    }
  }
}
</style>
